import React, { useEffect, useState } from 'react'
import { Button, Card, Col, Container, Row } from 'react-bootstrap'
import { Divider } from 'antd'
import { useSelector } from 'react-redux'
import axios from 'axios'

const Visits = () => {
  const patient = useSelector((state) => state.loggedInRoleUserData.loggedInRoleUserData)
  const [visits, setVisits] = useState([])

   //eslint-disable-next-line
   const getLastTwoVisits = async () => {
    try {
      console.log('fetching last two visits')
      const patientId = await patient.uniqueId
      const res = await axios.post('/api/v1/patient/patientVisitHistory', { patientId })
      if (res.data.success) {
        setVisits(res.data.data)
        console.log('fetching last two visits success')
      }
    } catch (error) {
      console.log(error)
      console.log('fetching last two visits error')
    }
  }
  useEffect(() => {
    getLastTwoVisits()
  }, [])
  return (
    <>
      <Container fluid className="shadow-sm rounded-2 p-3">
        <h4>Visits</h4>
        {/* Sample Starts from here */}
        {patient && visits && visits.length > 0 ? (
          visits.map((visit,idx) => (<Container key={idx} fluid as={Card} className='my-3'>
          <Row>
            <Card>
              {/* Date Containe */}
              <Container fluid className="d-flex justify-content-between mx-2 my-2">
                <div
                  style={{
                    fontSize: '1.1rem',
                  }}
                >
                  <b>Date</b> : {new Date(visit.updatedAt).toLocaleDateString()}
                </div>
                <div
                  style={{
                    fontSize: '1.1rem',
                  }}
                >
                  <b>Department</b> : Neurology
                </div>
              </Container>
              <Divider className="my-1" />

              {/* Prescription Containe */}
              <Container fluid>
                <Row className="d-flex justify-content-between">
                  <div className="col-12 col-lg-4 col-sm-5">
                    <h5>Presception</h5>
                    {visit.prescription}
                  </div>
                  <div className="col-12 col-lg-4 col-sm-5">
                    <h5>Summary</h5>
                   {visit.visitSummary}
                  </div>
                  <div className="col-12 col-lg-3 offset-lg-1 col-sm-5 offset-1 offset-sm-7 sm-my-5">
                    <b>Checked By :</b> Dr. {visit.checkedBy}
                  </div>
                </Row>
                <Container className="d-flex justify-content-end align-items-center flex-column flex-sm-column flex-md-row flex-lg-row">
                <Button className="m-2 btn-dark col-lg-2 col-md-2 col-12">Download</Button>
                <Button className="m-2 btn-dark col-lg-2 col-md-2 col-12">View</Button>
              </Container>
              </Container>
            </Card>
          </Row>
        </Container>))
        ) : (
          <>
            <Container>
            No data Found for visits
            </Container>
          </>
        ) }
   
      </Container>
    </>
  )
}

export default Visits
